<template>
	<view class="page">
		<scroll-view class="scrollView" ref="myScrollview" scroll-y="true" :refresher-triggered="showLoading"
			refresher-enabled="true" @refresherpulling="scrollTop" :scroll-with-animation="false"
			:scroll-into-view="toview">
			<view class="baseback" v-for="(mitem,mindex) in designList" v-show="mitem !== null" :key="mitem.id"
				:id="'s' + mitem.id">
				<view class="head">
					<image class="sculpture" src="../../static/bot_icon@2x.png"></image>
					<text class="name">爱俪Bot</text>
					<image class="mark" src="../../static/bot_mark@2x.png"></image>
					<text class="time">{{getTime(mitem.submitTime)}}</text>
					<view style="flex: 1;">
						<text v-show="mitem.action === 'IMAGINE' || mitem.action === 'REROLL'"
							:class="mitem.checked ? 'rselectButton' : 'rbutton'"
							@click="repitDesignClick(mitem,mindex)">
							重新设计
						</text>
					</view>

				</view>
				<view style="display: flex;flex-direction: row;width: 100%;">
					<view class="tips" style="margin-left: 40rpx;margin-right: 40rpx;">{{getDesignTitle(mitem)}}
						<text class="tips" v-show="mitem.progress === '100%'"
							style="color: #3C7FFF;background-color: rgb(60, 127, 255,0.1);margin-top: 0rpx; font-size: 24rpx;padding-left: 5rpx;padding-right: 5rpx;margin-left: 10rpx;">{{userName}}
						</text>
					</view>
				</view>
				<!-- <text
					style="max-width:90%;height:auto;margin-left: 40rpx;margin-right: 40rpx;font-size: 28rpx;">{{mitem.promptEn}}</text> -->
				<view class="baseImg" @click="clickImg(mitem)"
					:style="{opacity:(mitem.imageUrl && mitem.status === 'SUCCESS' && mitem.progress === '100%')?'1.0':'1.0'}">
					<u--image class="bigimage" v-if="!mitem.loading" :showLoading="true" :showError="false"
						:src="'http://img.yifuapp.wang/'+mitem.imageUrl" width="604rpx" height="604rpx"
						:lazy-load="true" :fade="true" duration="1500" radius='40rpx'>
						<template v-slot:loading>
							<u-loading-icon></u-loading-icon>
						</template>
					</u--image>
					<view class="bigbase"
						v-else-if="mitem.loading && (mitem.action === 'IMAGINE' || mitem.action === 'REROLL' || mitem.action ==='VARIATION')">
						<view class="indexTips">
							<view class="section">1</view>
							<view class="section">2</view>
							<view class="section">3</view>
							<view class="section">4</view>
						</view>
						<view class="loading">
							<u-loading-icon></u-loading-icon>
						</view>
					</view>
					<!-- <u-loading-icon v-show="mitem.loading && mitem.action !== 'IMAGINE'"></u-loading-icon> -->
				</view>
				<view v-show="mitem.action === 'IMAGINE' || mitem.action === 'REROLL' || mitem.action ==='VARIATION'"
					class="itemView" v-for="(item,index) in list" :key="index">
					<view class="itemView_title">{{item.name}}</view>
					<view class="itemView_grid" :border="false" :col="4">
						<view v-for="(citem,cindex) in item.childlist" :key="cindex">
							<button v-if="item.id === 'A'"
								:class="(mitem.downIndexs && mitem.downIndexs.includes(citem.id)) ? 'selectButton' : 'button'"
								@click="itemBtnClick(mitem,item,citem,mindex,cindex)">{{citem.name}}</button>
							<button v-if="item.id === 'B'"
								:class="(mitem.adjustIndexs && mitem.adjustIndexs.includes(citem.id)) ? 'selectRedButton' : 'button'"
								@click="itemBtnClick(mitem,item,citem,mindex,cindex)">{{citem.name}}</button>
						</view>
					</view>
				</view>
				<u-gap height="10"></u-gap>
			</view>
			<view class="bottomLine" id="tobottom" style="height: 1px;"></view>
		</scroll-view>

		<u-transition :duration="500" :mode-class="modeClass" :styles="transfromClass" :show="transShow" @click="onTap">
			<view class="tipview">点我开始女装设计</view>
		</u-transition>

		<view class="design" @click="startDesignClick">
			<image style="width: 36rpx;height: 36rpx;background-color: #ffffff;" src="../../static/icon_toadd@2x.png">
			</image>
		</view>
		<view class="tobottom" @click="toBottomClick">
			<image style="width: 36rpx;height: 36rpx;" src="../../static/icon_tobottom@2x.png"></image>
		</view>

		<template>
			<u-overlay :show="loading" opacity="0.3">
				<view class="overView">
					<u-loading-icon class="loadicon" color="#fff" size="30"></u-loading-icon>
				</view>
			</u-overlay>
			<u-overlay :show="showTipsLoading" opacity="0.3">
				<view class="overView">
					<view class="overTipsView">{{tipsMessages}}</view>
				</view>
			</u-overlay>
		</template>

	</view>
</template>

<script>
	import Config from "@/config.js"
	import Api from "@/api/aili/index.js"
	import pp from "@/utils/public.js"
	import moment from "moment/moment";
	import {
		data
	} from "../../uni_modules/uview-ui/libs/mixin/mixin";
	export default {
		data() {
			return {
				designList: [],
				list: [{
					id: "A",
					name: "下载高清大图",
					downIndex: 0,
					adjustIndex: 0,
					childlist: [{
						id: "1",
						pid: "A",
						name: "图1",
						isSelect: false
					}, {
						id: "2",
						pid: "A",
						name: "图2",
						isSelect: false
					}, {
						id: "3",
						pid: "A",
						name: "图3",
						isSelect: false
					}, {
						id: "4",
						pid: "A",
						name: "图4",
						isSelect: false
					}]
				}, {
					id: "B",
					name: "调整设计图",
					downIndex: 0,
					adjustIndex: 0,
					childlist: [{
						id: "1",
						pid: "B",
						name: "图1",
						isSelect: false
					}, {
						id: "2",
						pid: "B",
						name: "图2",
						isSelect: false
					}, {
						id: "3",
						pid: "B",
						name: "图3",
						isSelect: false
					}, {
						id: "4",
						pid: "B",
						name: "图4",
						isSelect: false
					}]
				}],
				toview: "",
				bottom: {
					id: "最底部"
				},
				insterest: "", //账户等级
				insterestCount: "0", //出图次数
				endTime: "", //结束时间
				designType: "", //设计类型
				taskId: null, //任务id
				timer: null,
				timerCount: 0,
				submitTimer: null,
				tipsMessages: null,
				showTipsLoading: false,
				showLoading: false,
				loading: false,
				index: 0,
				requestCount: 0,
				//动画相关
				transShow: false,
				modeClass: 'slide-right',
				transfromClass: {
					position: 'fixed',
					bottom: 0,
					top: 0,
					left: 0,
					right: 0,
					display: 'flex',
				},
				curPage: 1,
				pageCount: 1,
				isRequesting: false,
				downLoadIndex: null, //下载的单图的下标
				adjustIndex: null, //调整的单图的下标
				//设计细节数据
				designInfo: {
					taskId: null,
					shopType: null,
					shopTags: null,
					brand: null,
					base64Array: null,
					downLoadIndex: null,
					adjustIndex: null,
					reDesignIndex: null
				},
				topId: null,
				imgMode: "aspectFit"
			}
		},
		onShow(options) {
			this.token = uni.getStorageSync(this.$config.storage.token)
			if (this.token) {
				this.getLvInfo()
			}

			//开始设计
			if (this.$config.design.designInfo) {
				this.designInfo = Object.assign({}, this.$config.design.designInfo)
				console.log("设计数据=", this.designInfo)
				this.startDesign()

				this.$config.design.designInfo = null
			}
		},

		onLoad(options) {
			console.log("options=", options)
			//获取历史数据
			this.token = uni.getStorageSync(this.$config.storage.token)
			if (this.token) {
				this.getHistoryDesignInfo(false)
			}

			setTimeout(() => {
				this.transShow = true
			}, 2000)
		},
		onUnload() {

		},
		computed: {
			userName() {
				let userInfo = uni.getStorageSync(this.$config.storage.userInfo)
				if (userInfo) {
					return "@" + userInfo["nickname"]
				} else {
					return ""
				}

			}
		},
		methods: {
			// 设置节点Id
			setChildrenId(list) {
				var that = this;
				if (that.curPage === 1) { //通过页码来判断用户是否首次进入
					setTimeout(() => {
						that.$nextTick(() => {
							if (list.length != 0) {
								let index = list.length - 1;
								that.toview = 's' + list[index].id;
								console.log(that.toview, "首次进入需要滑动到页面最底部")
							}
						});
					}, 1000)
				} else {
					that.$nextTick(() => {
						that.toview = 's' + that.topId
						console.log(that.toview, "下拉刷新")
					});
				}
			},

			// 下拉加载更多聊天内容
			scrollTop(e) {
				if (!this.isRequesting) {
					this.showLoading = true

					this.topId = this.designList[0].id; //数据没改变前,记录最上方(第一条数据)的id,需要将它设置为节点
					console.log("topId1=", this.topId)

					//计算刷新第几页的数据
					let page = this.designList.length % 10
					let count = Math.floor(this.designList.length / 10)
					this.curPage = (count < 1 ? 1 : count) + 1
					console.log("当前页数=", this.designList.length, this.curPage)

					this.getHistoryDesignInfo(true);
				}
			},

			startDesign() {
				let taskId = this.designInfo.taskId ? this.designInfo.taskId : null
				let index = this.designInfo.adjustIndex ? this.designInfo.adjustIndex : null
				let shop_type = this.designInfo.shopType
				let shop_tags = this.designInfo.shopTags
				let brand = this.designInfo.brand
				let base64Array = this.designInfo.base64Array ? this.designInfo.base64Array : null

				this.submitTask(index, taskId, shop_type, shop_tags, brand, base64Array)
			},
			//获取账户等级
			getLvInfo() {
				pp.getInstence(this).then(res => {
					console.log("insterest=", res)
					this.insterest = res.detail.lvName
					this.insterestCount = res.detail.number
					this.endTime = res.detail.endTime
				})
			},

			checkQuanxian() {
				console.log("当前时间=", Math.abs(new Date().getTime()))
				console.log("结束时间=", this.endTime)
				let time = true
				let count = true

				if (this.endTime < Math.abs(new Date().getTime())) {
					this.$u.toast("您的设计出图次数已过有效期")
					setTimeout(() => {
						this.$nav.to("/pages/shouye/accountEquity")
					}, 1000)
					time = false
				} else if (this.insterestCount <= 0) {
					this.$u.toast("您的设计出图次数已不足")
					setTimeout(() => {
						this.$nav.to("/pages/shouye/accountEquity")
					}, 1000)
					count = false
				}

				return time && count
			},

			//点击下载、调整按钮
			itemBtnClick(mitem, item, citem, mindex, cindex) {

				if (mitem.loading) {
					this.$u.toast("正在生成设计图，请稍后！")
					return
				}

				if (this.checkQuanxian()) {
					if (item.id === "A") {
						if (!mitem.downIndexs.includes(citem.id)) {
							mitem.downIndexs.push(citem.id)
						}
					} else if (item.id === "B") {
						if (!mitem.adjustIndexs.includes(citem.id)) {
							mitem.adjustIndexs.push(citem.id)
						}
					}

					if (!citem.checked) {
						this.$set(citem, "checked", true)
					}

					let taskId = mitem.id
					let index = citem.id


					let designInfo = this.getDesignInfoData(mitem, mindex, citem.id)
					this.$set(mitem, "designInfo", designInfo)
					this.downLoadIndex = null
					this.adjustIndex = null

					if (item.id === "A") { //下载单图
						console.log("下载单图")
						this.designType = this.$config.designType.CHANGE
						this.qequestCount = 0
						this.downLoadIndex = index

						this.submitTask(index, taskId, designInfo.shopType, designInfo.shopTags, designInfo.brand,
							designInfo.base64Array)

					} else { //调整单图
						console.log("调整单图")
						this.designType = this.$config.designType.CHANGE
						this.adjustIndex = index

						// if (this.insterest != "Max" && this.insterest != "Plus") {
						// 	this.$u.toast("您的账户等级不支持该功能，请升级为PLUS或MAX用户。")
						// 	setTimeout(() => {
						// 		this.$nav.to("/pages/shouye/accountEquity")
						// 	}, 1000)
						// } else {

						this.$config.design.adjust = true
						this.$config.design.adjustIndex = index
						this.$config.design.taskId = taskId

						console.log("designInfo=", designInfo)
						this.$nav.to("/pages/shouye/designDetails", {
							designInfo: JSON.stringify(designInfo)
						}, false)
						// }
					}
				}

			},
			//重新设计
			repitDesignClick(item, index) {
				if (item.loading) {
					this.$u.toast("正在生成设计图，请稍后！")
					return
				}

				if (this.checkQuanxian()) {
					if (!item.checked) {
						this.$set(item, "checked", true)
					}

					item.reDesign = true
					this.designType = this.$config.designType.CHANGE
					this.$config.design.startDesign = true
					this.downLoadIndex = null
					this.adjustIndex = null

					let designInfo = this.getDesignInfoData(item, index, null)
					this.$nav.to("/pages/shouye/designDetails", {
						designInfo: JSON.stringify(designInfo)
					}, false)
				}
			},
			getDesignInfoData(item, rIndex, dIndex) {
				let designInfo = {
					taskId: null,
					shopType: null,
					shopTags: null,
					brand: null,
					base64Array: null,
					downLoadIndex: null,
					adjustIndex: null,
					reDesignIndex: null
				}

				designInfo.taskId = item.id
				designInfo.reDesignIndex = rIndex
				designInfo.adjustIndex = dIndex
				if (item.state) {
					let obj = JSON.parse(item.state)
					designInfo.shopType = obj.shopType + ""
					designInfo.shopTags = obj.shopTags
					designInfo.brand = obj.brand

					if (obj.base64Array) {
						designInfo.base64Array = Array.isArray(obj.base64Array) ? obj.base64Array[0] : obj.base64Array
					}
				}
				//测试用后面删除
				// if (item.prompt) {
				// 	let prompt = item.prompt.split(",")
				// 	designInfo.brand = prompt[0];
				// }
				console.log("重组后的designInfo=", designInfo)
				return designInfo
			},
			//开始设计
			startDesignClick() {
				this.designType = this.$config.designType.IMAGINE
				this.downLoadIndex = null
				this.adjustIndex = null

				if (this.checkQuanxian()) {
					this.$config.design.startDesign = true
					this.$nav.to("/pages/shouye/selectClass")
				}
			},
			//滑动到最底部
			toBottomClick() {
				this.toview = "tobottom"
				this.showLoading = false
				this.loading = false
				this.$ch.sleep(1000).then(() => {
					this.toview = "asfkaj"
				})
			},

			//生成图片
			submitTask(index, taskId, shop_type, shop_tags, brand, base64Array) {
				console.log("shop_type = ", shop_type)
				console.log("shop_tags = ", shop_tags)
				console.log("brand = ", brand)

				if (!shop_type || shop_tags.length == 0 || !brand) {
					return
				}

				this.showTipsLoading = false
				this.loading = true
				this.index = index
				this.taskId = taskId

				let params = {}
				//job类型 *  imagine *  change *  describe *  blend 
				params["type"] = taskId ? "change" : "imagine"
				//比例 * blend 可选 PORTRAIT(2:3); SQUARE(1:1); LANDSCAPE(3:2),可用值:PORTRAIT,SQUARE,LANDSCAPE,示例值(SQUARE) |
				params["dimensions"] = "LANDSCAPE"
				//垫图base64数组 ["data:image/png;base64,xxx1"]~~ 垫图又拍云访问地址（上传接口中的url去除域名）
				params["base64Array"] = Array.isArray(base64Array) ? base64Array[0] : base64Array
				//绘图变化 * change类型 必传 UPSCALE(放大); VARIATION(变换); REROLL(重新生成),可用值:UPSCALE,VARIATION,REROLL,示例值(UPSCALE) |
				params["action"] = null
				//序号 * change类型 可选 序号(1~4), action为UPSCALE,VARIATION时必传,示例值(1) 
				params["index"] = null
				//任务id * change类型 必传       
				params["taskId"] = null
				//分类id, state字段成员，如上衣类目下的小衫，则数据为  283  
				params["state.shopType"] = shop_type
				//属性id, state字段成员，如白色系下白色和乳白色,风格下的甜美，则数据为  [531,7715]
				params["state.shopTags"] = shop_tags.join(",")
				//品牌值
				params["state.brand"] = brand

				if (this.designType === this.$config.designType.CHANGE) {

					params["taskId"] = taskId

					//如果有index是调整没有则是重新生成
					if (this.downLoadIndex) {
						params["action"] = "UPSCALE"
					} else if (this.adjustIndex) {
						params["action"] = "VARIATION"
					} else {
						params["action"] = "REROLL"
					}

					//如果是重新生成不传index
					if (params["action"] !== "REROLL") {
						params["index"] = index
					}
				}

				console.log("params=", params)
				this.$http.get(Api.subTask, {
					params: params
				}).then(result => {
					console.log('result', result);
					if (result.status === "1") { //请求成功
						if (result.code == 23) { //排队
							this.showTipsLoading = true
							this.tipsMessages = "现在有点忙，请20分钟后再回来提交。"
							// setTimeout(() => {
							// 	this.submitTask(this.index, taskId, shop_type, shop_tags, brand,
							// 		base64Array)
							// }, 300 * 1000)

						} else if (result.code == 22) { //等待
							this.showTipsLoading = true
							this.tipsMessages = "现在有点忙，需要等5-20分钟，可以稍晚回来看进度哦。"
							setTimeout(() => {
								this.submitTask(this.index, taskId, shop_type, shop_tags, brand,
									base64Array)
							}, 120 * 1000)
						} else if (result.code == 1) { //生成
							this.showTipsLoading = false
							if (result.message === "已存在") {
								uni.hideLoading()
								this.loading = false
								var index = this.designList.findIndex(item => {
									if (item.id == result.taskId) {
										return true
									}
								})
								if (index) {
									this.$u.toast(result.message)
									this.toview = 's' + this.designList[index].id
								} else {
									this.$u.toast("已存在请在历史记录中查看")
								}

								return
							}
							if (result.taskId) {
								this.taskId = result.taskId
								this.requestCount = 0
								this.getDesignInfo(result.taskId, false, index ? true : false)

								this.$config.design.startDesign = false
								this.$config.design.adjust = false
								this.$config.design.taskId = null
								this.$config.design.adjustIndex = null

								uni.$off("startDesign")
							} else {
								uni.hideLoading()
								this.loading = false
								this.$u.toast(result.message)
							}
						}

					} else { //请求失败
						uni.hideLoading()
						this.$u.toast(result.message)
						this.loading = false
						this.showTipsLoading = false
					}
				}).catch(er => {
					console.log('er', er);
					uni.hideLoading()
					this.loading = false
					this.showTipsLoading = false
					this.$u.toast(er.message)
				}).finally(() => {

				})
			},

			//获取绘图任务信息
			getDesignInfo(taskId, timer, single) {
				this.$http.get(Api.designInfo, {
					params: {
						taskId: taskId
					}
				}).then(result => {
					console.log('result', result);

					if (result.status === "1" && result.detail) {


						let data = result.detail[0]
						if (data != null && (data.status === "SUCCESS" || data.status === "FAILURE")) {
							if (data.status === "FAILURE") {
								this.$u.toast(data["failReason"])
								this.stop()
							} else {

								if (this.requestCount > 0) {
									this.stop()
								} else {
									if (!this.timer) {
										this.timerCount = 0
										this.start()
									}
								}

								if (data.progress == "100%") {
									this.requestCount++
								}
							}
						} else {
							if (!this.timer) {
								this.timerCount = 0
								this.start()
							}
						}


						if (data.id) {
							this.tipsMessages = 'AI设计出图可能需要1-10分钟，请耐心等待。'
							this.showTipsLoading = true
							if (data.progress == "100%" || data.status === "FAILURE") {
								this.showTipsLoading = false
							}

							this.$set(data, "taskId", taskId)
							this.$set(data, "loading", (data.progress == "100%" || data.status === "FAILURE") ?
								false : true)
							this.$set(data, "downIndexs", [])
							this.$set(data, "adjustIndexs", [])

							if (this.downLoadIndex) {
								this.$set(data, "downIndex", this.downLoadIndex)
							}

							if (this.adjustIndex) {
								this.$set(data, "adjustIndex", this.adjustIndex)
							}


							let findIndex = null
							this.designList.forEach((item, index) => {
								if (item.id === data.id) {
									findIndex = index
								}
							})


							//没有就添加，有就替换
							if (findIndex !== null) {
								this.$set(this.designList, findIndex, data)
							} else {
								this.designList.push(data)
								setTimeout(() => {
									uni.hideLoading()
									this.toBottomClick()
								}, 1000);
							}

							console.log("最终的数据=", this.designList)
						}
					} else {
						this.loading = false
						this.showTipsLoading = false
						uni.hideLoading()
					}
				}).catch(er => {
					console.log('er', er);
					this.showTipsLoading = false
					// this.$u.toast(er.message)
				}).finally(() => {

				})
			},

			//获取历史绘图数据
			getHistoryDesignInfo(isMore) {

				this.isRequesting = true
				this.$http.get(Api.designInfo + "?pager.sort=_id&pager.order=desc", {
					params: {
						taskId: null,
						curPage: this.curPage
					}
				}).then(result => {
					console.log('result', result);

					if (result.status === "1" && result.detail) {
						this.pageCount = result.pager.pageCount
						let dataList = []
						let list = result.detail.reverse()
						list = list.filter(item => {
							return item !== null
						})
						list.forEach((item, index) => {
							if (item !== null) {
								this.$set(item, "downIndexs", [])
								this.$set(item, "adjustIndexs", [])

								if (item.action === "IMAGINE" || item.action === "REROLL" || item
									.action === "VARIATION") {
									this.$set(item, "reDesign", false)

									item.downIndexs = item.subtask.map(sub => {
										if (sub.action === "UPSCALE") {
											return sub.index
										}
									})

									item.adjustIndexs = item.subtask.map(sub => {
										if (sub.action === "VARIATION") {
											return sub.index
										}
									})

									let arr = item.subtask.filter(sub => {
										return sub.action === "REROLL"
									})
									item.checked = arr.length > 0
								}
							}
						})

						// dataList = [...list, ...this.designList]

						dataList = list.concat(this.designList)

						//在数组对象后添加
						dataList = this.elimination(dataList)

						this.designList = [...dataList]

						console.log("历史数据=", this.designList)

						this.setChildrenId(dataList)
						this.$forceUpdate()
					}

					setTimeout(() => {
						uni.hideLoading()
						this.showLoading = false
						this.isRequesting = false
					}, 1000);
				}).catch(er => {
					console.log('er', er);
					uni.hideLoading()
					this.showLoading = false
				}).finally(() => {
					uni.stopPullDownRefresh()
				})
			},

			//封装后的去重
			elimination(arr) {
				const res = new Map();
				return arr.filter((arr) => !res.has(arr.id) && res.set(arr.id, 1));
			},

			getDesignTitle(item) {
				if (item.progress === "100%") {
					if (item.state) {
						let obj = JSON.parse(item.state)
						if (item.action === "UPSCALE") {
							item.downIndex = obj.index
						} else if (item.action === "VARIATION") {
							item.adjustIndex = obj.index
						}
					}

					if (item.downIndex) {
						return `已生成设计图${item.seed ? item.seed : ""}图${item.downIndex ? item.downIndex : 1}的高清大图  `
					} else if (item.adjustIndex) {
						return `已生成设计图${item.seed ? item.seed : ""}图${item.adjustIndex ? item.adjustIndex : 1}的调整设计图  `
					} else
						return `已生成设计图${item.seed ? item.seed : ""}`
				} else {
					return `正在生成设计图，请稍等（${item.progress ? item.progress : "0%"})`
				}
			},

			//定时提交绘图任务
			submitStart: function(time, index, taskId, shop_type, shop_tags, brand, base64Array) {
				var that = this
				that.submitEnd()
				that.submitTimer = setInterval(function() {
					that.submitTask(index, taskId, shop_type, shop_tags, brand, base64Array)
				}, time * 1000)
			},
			submitEnd: function() {
				var that = this
				if (that.submitTimer) {
					clearInterval(that.submitTimer)
					that.submitTimer = null
				}
			},
			//定时获取绘图任务信息
			start: function() {
				var that = this
				that.timer = setInterval(function() {
					if (that.timerCount >= 100) {
						that.stop()
						return
					}
					that.timerCount++
					that.getDesignInfo(that.taskId, false)

				}, 5000)
			},
			stop: function() {
				uni.hideLoading()
				this.loading = false
				this.showTipsLoading = false
				var that = this
				if (that.timer) {
					clearInterval(that.timer)
					that.timer = null
				}
			},
			getTime(date) {
				return date ? moment(date).format('YYYY･MM･DD HH:mm') : "";
			},

			onTap() {
				this.modeClass = 'fade'
				this.transShow = false;

				setTimeout(() => {
					this.startDesignClick()
				}, 800)
			},

			//点击图片放大
			clickImg(item) {
				console.log("点击的下标=", item.imageUrl)
				let url = this.$config.imgUrl + item.imageUrl
				uni.previewImage({
					urls: [url], //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					current: '', // 当前显示图片的http链接，默认是第一个
					success: function(res) {},
					fail: function(res) {},
					complete: function(res) {},
				})
			},
		}
	}
</script>

